<template>
    <div class="container">
        <div class="tabs">
            <ul>
                <li v-for="(tab, index) in tabs" :key="index" 
                    @click="tabClick(index)" :class="{'is-active': $route.query.list == tab.list || $route.query.tab == 'list_order'}">
                    <a>{{tab.name}}</a>
                </li>
            </ul>
        </div>
        <component :is="tabs[currentTab].comps"></component>
    </div>
</template>

<script>
import Waiters from './Manage-Waiter';
import Product from './Manage-Product';
import ProductType from './Manage-Product-Type';
import Cashier from './Manage-Cashier';
import Kitchen from './Manage-Kitchen';

export default {
    components:{
        Waiters,
        ProductType,
        Product,
        Cashier,
        Kitchen,
    },
    data: ()=>({
        currentTab : 0,
        tabs: [
            {name: "ຂໍ້ມູນ ຜູ້ເສີບອາຫານ", comps:"Waiters", list: 'waiter'},
            {name: "ຂໍ້ມູນ  ເເຄ໋ດເຊຍ", comps:"Cashier", list: 'cashier'},
            {name: "ຂໍ້ມູນ  ຫ້ອງຄົວອາຫານ", comps:"Kitchen", list: 'kitchen'},
            {name: "ຂໍ້ມູນ  ປະເພດເມນູ", comps:"ProductType", list: 'product_type'},
            {name: "ຂໍ້ມູນ  ລາຍການເມນູ", comps:"Product", list: 'product'},
        ],
    }),
    methods:{
        tabClick(index){
            this.currentTab = index;
            
            if(index == 0){
                this.$router.push({ query: { ...this.$route.query, list: 'waiter' }}).catch(()=>{});
            }else if(index == 1){
                this.$router.push({ query: { ...this.$route.query, list: 'cashier' }}).catch(()=>{});
            }else if(index == 2){
                this.$router.push({ query: { ...this.$route.query, list: 'kitchen' }}).catch(()=>{});
            }else if(index == 3){
                this.$router.push({ query: { ...this.$route.query, list: 'product_type' }}).catch(()=>{});
            }else if(index == 4){
                this.$router.push({ query: { ...this.$route.query, list: 'product' }}).catch(()=>{});
            }
        }
    },
    created() {
        if(!this.$route.query.list){
            this.$router.push({ query: { ...this.$route.query, list: 'waiter', tab: 'tab_manage' }}).catch(()=>{});
        }else if(this.$route.query.list == 'waiter'){
            this.currentTab = 0;
        }else if(this.$route.query.list == 'cashier'){
            this.currentTab = 1;
        }else if(this.$route.query.list == 'kitchen'){
            this.currentTab = 2;
        }else if(this.$route.query.list == 'product_type'){
            this.currentTab = 3;
        }else if(this.$route.query.list == 'product'){
            this.currentTab = 4;
        }
    },
}
</script>

<style lang="scss" scoped>

.tabs{
    margin-top: 30px;
    ul{
        border-bottom: none;
    }
    a{
        border-bottom-color: $white;
    }
    li.is-active a{
        box-shadow: none;
        border: none;
        background: $crimson;
        color: $white;
        border-radius: 5px;
        transition: all ease-in-out 0.3s;
    }
}
</style>