<template>
    <div class="container">
        <div class="tabs">
            <ul>
                <li v-for="(tab, index) in tabs" :key="index" 
                    @click="tabClick(index)" :class="{'is-active': $route.query.list == tab.list}">
                    <a>{{tab.name}}</a>
                </li>
            </ul>
        </div>
        <component :is="tabs[currentTab].comps"></component>
    </div>
</template>

<script>
import ListOrdered from './List-Ordered';

export default {
    components:{
        ListOrdered,
    },
    data: ()=>({
        currentTab : 0,
        tabs: [
            {name: "ລາຍການ ລໍຖ້າຊຳລະເງີນ", comps:"ListOrdered", list: 'ordered'},
        ],
    }),
    methods:{
        tabClick(index){
            this.currentTab = index;
            if(index == 0){
                this.$router.push({ query: { ...this.$route.query, list: 'ordered' }}).catch(()=>{});
            }
        }
    },
    created() {
        if(!this.$route.query.list){
            this.$router.push({ query: { ...this.$route.query, list: 'ordered' }}).catch(()=>{});
        }
    },
}
</script>

<style lang="scss" scoped>

.tabs{
    margin-top: 30px;
    ul{
        border-bottom: none;
    }
    a{
        border-bottom-color: $white;
    }
    li.is-active a{
        box-shadow: none;
        border: none;
        background: $crimson;
        color: $white;
        border-radius: 5px;
        transition: all ease-in-out 0.3s;
    }
}
</style>