export default {
    data:()=>({
        isAddActive: false,
        isEditActive: false,
        isViewActive: false,
        isDeleteActive: false,
        isLoading: false,

        value: {},
        listData: [],

        units: [],
        valUnit: {},
        productTypes: [],
        valProductType: {},

        selectedFile: '',
        fileName: '',
    }),

    methods: {
        triggerInputClick() {
            let selector = this.$refs.file;
            if (selector) {
                selector.click();
            }
        },
        async onFileChange($event) {
            const file = $event.target.files[0];
            this.UploadFile(file);
        }, // OnFile-Change

        UploadFile(file){
            let formData = new FormData();
            formData.append('imageFile', file);

            this.$axios.post('upload-file', formData).then(res=>{
                this.selectedFile = res.data.fileName;
                this.fileName = res.data.fileName;
            });
        }, // Upload-File

        ValidateForm(scope){
            this.$validator.validateAll(scope).then((result) => {
                if (result) {
                    if(scope == 'form-add'){
                        this.isLoading = true;
                        this.AddItem();
                    }else if(scope == 'form-edit'){
                        this.isLoading = true;
                        this.EditItem();
                    }
                }
            });
        }, // Validate-Form

        AddItem(){
            this.value.file = this.selectedFile;
            this.value.unit = this.valUnit.id;
            this.value.type = this.valProductType.id;

            this.$axios.post('add-products', this.value).then(res=>{
                if(res.data.success == true){
                    setTimeout(()=>{
                        this.isLoading = false;
                        this.FetchData();
                        this.CloseModal();
                        this.value = {};
                    }, 300);
                }
            }).catch(()=>{
                this.isLoading = false;
                this.CloseModal();
            });
        }, // Add-Waiter

        DeleteItem(){
            // this.isLoading = true;
            // this.$axios.get('delete-kitchens/' + this.value).then(res=>{
            //     if(res.data.success == true){
            //         setTimeout(() => {
            //             this.isLoading = false;
            //             this.FetchData();
            //             this.CloseModal();
            //         }, 300);
            //     }
            // }).catch(()=>{
            //     this.isLoading = false;
            //     this.FetchData();
            //     this.CloseModal();
            // });
        }, // Delete-Modal

        EditItem(){
            this.value.file = this.selectedFile;
            this.$axios.post('edit-products/' + this.value.id, this.value).then(res=>{
                if(res.data.success == true){
                    setTimeout(()=>{
                        this.isLoading = false;
                        this.FetchData();
                        this.CloseModal();
                        this.value = {};
                    }, 300);
                }
            }).catch(()=>{
                this.isLoading = false;
                this.CloseModal();
            });
        }, // Edit-Waiter

        CallModalAdd(){
            this.value = {},
            this.isAddActive = true;
        }, // Call-Modal-Add

        CallModalEdit(index){
            this.value = {...this.listData[index]};
            this.isEditActive = true;
        }, // Call-Modal-Edit

        CallModalView(index){
            this.value = {...this.listData[index]};
            this.isViewActive = true;
        }, // Call-Modal-View

        CallModalDelete(id){
            this.value = id;
            this.isDeleteActive = true;
        }, // Call-Modal-Delete

        CloseModal(){
            this.isAddActive = false;
            this.isEditActive = false;
            this.isViewActive = false;
            this.isDeleteActive = false;
        }, // Close-Modal

        FetchData(){
            this.$axios.get('list-products').then(res=>{
                this.listData = res.data.products;
                this.units = res.data.units;
                this.valUnit = {...this.units[0]};
            }).catch(()=>{
                
            });

            this.$axios.get('list-product-types').then(res=>{
                this.productTypes = res.data.product_type;
                this.valProductType = {...this.productTypes[0]};
            }).catch(()=>{
                
            });
        }, // FetchData
        
    },
}