<template>
    <div class="section">
        <div class="columns box-register">
            <div class="column is-4-desktop is-8-tablet is-offset-4-desktop is-offset-2-tablet is-12-mobile is-offset-0-mobile">
                <div class="card">
                    <div class="img">
                        <img src="../assets/logo-mfood.jpg">
                    </div>
                    <div class="title-register">
                        <p>
                            ລະບົບຮ້ານອາຫານ
                        </p>
                    </div>
                    <p class="msg-error">{{ $store.state.msg_error_login }}</p>
                    <div class="card-content" :style="{ marginTop: '2px' }">
                        <div class="content">
                            <div class="field">
                                <label class="label">ອີເມວ <span>*</span>
                                    <span class="text-error">
                                        {{ errors.first('email') }}
                                    </span>
                                </label>
                                <div class="control">
                                    <input name="email" 
                                        class="input input-ctn" 
                                        type="text" 
                                        placeholder="ອີເມວ..."
                                        v-model="email"
                                        v-validate="'required|email'" 
                                    >
                                </div>
                            </div>
                            <div class="field">
                                <label class="label">ລະຫັດຜ່ານ <span>*</span>
                                    <span class="text-error">
                                        {{ errors.first('password') }}
                                    </span>
                                </label>
                                <div class="control">
                                    <input name="password" 
                                        class="input input-ctn" 
                                        type="password" 
                                        placeholder="ລະຫັດຜ່ານ..."
                                        v-model="password"
                                        v-validate="'required|max:20|min:6'" 
                                    >
                                </div>
                            </div>
                            
                            <div class="field box-btn-register">
                                <button class="button" @click="ValidateForm()" 
                                    :class="{'is-loading': btn_loading}">
                                    <i class="fal fa-key" v-if="!btn_loading"></i> 
                                    ເຂົ້າສູ່ລະບົບ
                                </button>
                            </div><br>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Validator } from 'vee-validate';


// Function Validate Form
const dict = {
  custom: {
    password:{
        required: 'ກະລຸນາປ້ອນລະຫັດຜ່ານກ່ອນ...',
        min: 'ລະຫັດຜ່ານບໍ່ຄວນສັ້ນກວ່າ 6 ໂຕ...',
        max: 'ລະຫັດຜ່ານບໍ່ຄວນເກີນ 20 ໂຕ...',
    },
    email: {
        required: 'ກະລຸນາປ້ອນອີເມວກ່ອນ...',
        email: 'ອີເມວບໍ່ຖືກຕ້ອງ...',
    }
  }
};
Validator.localize('en', dict);


export default {
    data: ()=>({
        email: '',
        password: '',
        btn_loading: false,
    }),
    methods:{
        ValidateForm(){ 
            this.$validator.validateAll().then((result) => {
                if (result) {
                    this.SignIn();
                }
            });
        },

        SignIn(){
            this.btn_loading = true;
            this.$store.dispatch('RestaurantSignIn', {
                email: this.email,
                password: this.password,
            }).then(response => {
                if(response.data.success == true){
                    setTimeout(()=>{
                        this.btn_loading = false;
                        // this.$toasted.show(`ເຂົ້າສູ່ລະບົບສຳເລັດເເລ້ວ...`, {
                        //     icon: "fad fa-key mr-5px has-text-danger",
                        //     duration: 3000
                        // });
                    }, 200);
                }else{
                    this.btn_loading = false;
                }
            }).catch(() => {
                this.btn_loading = false;
            });
        }
    }
}
</script>

