export default {
    data:()=>({
        isAddActive: false,
        isEditActive: false,
        isViewActive: false,
        isDeleteActive: false,
        isLoading: false,

        value: {},
        listData: [],
    }),

    methods: {

        ValidateForm(scope){
            this.$validator.validateAll(scope).then((result) => {
                if (result) {
                    if(scope == 'form-add'){
                        this.isLoading = true;
                        this.AddItem();
                    }else if(scope == 'form-edit'){
                        this.isLoading = true;
                        this.EditItem();
                    }
                }
            });
        }, // Validate-Form

        AddItem(){
            this.$axios.post('add-cashiers', this.value).then(res=>{
                if(res.data.success == true){
                    setTimeout(()=>{
                        this.isLoading = false;
                        this.FetchData();
                        this.CloseModal();
                        this.value = {};
                    }, 300);
                }
            }).catch(()=>{
                this.isLoading = false;
                this.CloseModal();
            });
        }, // Add-Waiter

        DeleteItem(){
            this.isLoading = true;
            this.$axios.get('delete-cashiers/' + this.value).then(res=>{
                if(res.data.success == true){
                    setTimeout(() => {
                        this.isLoading = false;
                        this.FetchData();
                        this.CloseModal();
                    }, 300);
                }
            }).catch(()=>{
                this.isLoading = false;
                this.FetchData();
                this.CloseModal();
            });
        }, // Delete-Modal

        EditItem(){
            this.$axios.post('edit-cashiers/' + this.value.id, this.value).then(res=>{
                if(res.data.success == true){
                    setTimeout(()=>{
                        this.isLoading = false;
                        this.FetchData();
                        this.CloseModal();
                        this.value = {};
                    }, 300);
                }
            }).catch(()=>{
                this.isLoading = false;
                this.CloseModal();
            });
        }, // Edit-Waiter

        CallModalAdd(){
            this.value = {},
            this.isAddActive = true;
        }, // Call-Modal-Add

        CallModalEdit(index){
            this.value = {...this.listData[index]};
            this.isEditActive = true;
        }, // Call-Modal-Edit

        CallModalView(index){
            this.value = {...this.listData[index]};
            this.isViewActive = true;
        }, // Call-Modal-View

        CallModalDelete(id){
            this.value = id;
            this.isDeleteActive = true;
        }, // Call-Modal-Delete

        CloseModal(){
            this.isAddActive = false;
            this.isEditActive = false;
            this.isViewActive = false;
            this.isDeleteActive = false;
        }, // Close-Modal

        FetchData(){
            this.$axios.get('list-cashiers').then(res=>{
                this.listData = res.data.cashiers;
            }).catch(()=>{

            });
        }, // FetchData
        
    },
}